import { h, Component } from 'preact';
import { Router } from 'preact-router';

import Header from './header';

// Code-splitting is automated for routes
import Home from '../routes/home';
import Profile from '../routes/profile';

const Stat2 = ({ title, value }) => (
    <span>
        <span class="title">{title}: </span>
        {value}
        &nbsp;
    </span>
);

const Footer = ({ fps, ms, stats }) => (
    <footer class="footer">
        <Stat2 title="fps" value={Math.round(fps)} />
        <Stat2 title="ms" value={ms.toFixed(1)} />
        {stats.map(({ title, value }) => (
            <Stat2 title={title} value={value} />
        ))}
    </footer>
);

const Stat = ({ title, value }) => (
    <div>
        <span class="title">{title}:</span> {value}
    </div>
);

// <Stat title="ms" value={`${ms.toFixed(1)} (${(ms * 6) | 0}%)`} />

const Stats = ({ fps, ms, stats }) => (
    <div id="stats">
        <Stat title="fps" value={Math.round(fps)} />
        <Stat title="ms" value={ms.toFixed(1)} />
        {stats.map(({ title, value }) => (
            <Stat title={title} value={value} />
        ))}
    </div>
);

export default class App extends Component {
    state = {
        loop: null,
        fps: 0,
        ms: 0,
        stats: [],
    };

    handleRoute = e => {
        this.currentUrl = e.url;
        this.setState({ loop: null });
    };

    begin = () => {
        this.beginTime = (performance || Date).now();
    };

    end = () => {
        this.frames++;

        const now = (performance || Date).now();

        if (now >= this.prevTime + 1000) {
            const ms = now - this.beginTime;

            this.setState({ ms });
            const fps = (this.frames * 1000) / (now - this.prevTime);
            this.prevTime = now;
            this.frames = 0;
            this.setState({ fps });
        }
    };

    start = loop => {
        this.setState({ loop });
        this.loop();
    };

    loop = () => {
        if (this.state.loop) {
            this.begin();
            const stats = this.state.loop();
            this.setState({ stats });
            this.end();
            requestAnimationFrame(this.loop);
        }
    };

    componentDidMount() {
        this.beginTime = (performance || Date).now();
        this.prevTime = this.beginTime;
        this.frames = 0;
    }

    render(props, { fps, ms, stats, loop }) {
        return (
            <div id="app">
                <Header />
                {/*loop && <Stats fps={fps} ms={ms} stats={stats} />*/}
                <Router onChange={this.handleRoute}>
                    <Home path="/" start={this.start} />
                    <Profile path="/profile/" user="me" />
                    <Profile path="/profile/:user" />
                </Router>
                {loop && <Footer fps={fps} ms={ms} stats={stats} />}
            </div>
        );
    }
}



